.scribe-container {
    display: flex;
    flex-direction: column;
    padding: 15px; // Padding for the overall container
    gap: 10px; // Gap between elements
    max-height: 100dvh; // Keep the overall container height at 100% of the viewport

    .top-container {
        max-height: 20dvh; // Set height to 20% of the viewport height
        flex-shrink: 0; // Prevent shrinking of the top container
        display: flex;
        flex-direction: column;

        .patientName {
            font-weight: 600;
        }

        .detailsContainer {
            display: flex;
            gap: 10px;
        }

        .tabContainer {
            margin-top: 15px; // Margin for the tab container
            display: flex;
            gap: 10px;
            overflow: auto;
            flex-wrap: wrap;

            .tabOption {
                border: 1px solid #000;
                padding: 5px 20px;
                display: flex;
                align-items: center;

                &:hover {
                    cursor: pointer;
                    background: rgba(0, 0, 0, 0.2);
                }

                &.selectedTab {
                    border-bottom: 5px solid #000;
                }
            }
        }
    }

    .recordingContainer {
        display: flex;
        overflow: auto;
        gap: 20px;
        flex-direction: column; // Stack children vertically
        flex-grow: 1; // Allows it to take remaining space
        border: 1px solid #000;
        overflow: auto; // Enable scrolling if content exceeds

        .innerRecordingContainer {
            flex-grow: 1;
            display: flex;

            .sessionData {
                display: flex;
                flex-direction: column;
                margin: auto;
                height: 100%;
                justify-content: space-around;

                .timerContainer {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }

                .profile {
                    padding: 30px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 80px;
                    border-radius: 50%;
                    height: 80px;
                    background: rgb(242, 193, 252);

                    & p {
                        font-size: 35px;
                    }
                }

                .animationContainer {
                    padding: 10px;
                }
            }
        }

        .textContainer {
            padding: 10px;
        }


        .buttonContainer {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 30px;
            background: #cccccc4f;
            height: 8dvh;
            gap: 20px;

            & button {
                padding: 10px 20px;
                border-radius: 20px;
            }

        }
    }
}