* {
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
}

:root {
  --primary-color: #66A8CF;
  --secondary-color: #C38EB4;
  --text-color: #232F47;
  --background-color: #E1CBD7;
  --button-hover-color: #5B97B5;
}

.custom-scrollbar {
  overflow-y: auto;
}

.custom-scrollbar::-webkit-scrollbar {
  display: none;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: lightgreen;
  border-radius: 10px;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #26425A;
}

.custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: #66A8CF #f1f1f1;
}

.walkinButton:hover {
  cursor: pointer;
}

.initButton {
  position: fixed;
  right: 40px;
  display: flex;
  align-items: center;
  bottom: 40px;
  background-color: #4CAF50;
  color: #fff;
  padding: 10px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.react-ext-container {
  animation: slideUp 0.6s cubic-bezier(0.25, 0.8, 0.25, 1) forwards;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 12px;
  bottom: -100%;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  height: 60vh;
  overflow: auto;
  position: fixed;
  right: 20px;
  width: 40vw;
  z-index: 1000000;
  transform: scale(0.95);
  opacity: 0;
}

@keyframes slideUp {
  0% {
    bottom: -50%;
    transform: scale(0.95);
    opacity: 0;
  }

  60% {
    bottom: 3%;
    transform: scale(1.02);
    opacity: 1;
  }

  100% {
    bottom: 2%;
    transform: scale(1);
    opacity: 1;
  }
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}