.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--primary-color);
    color: white;
    padding: 1rem 2rem;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 99999;
    right: 0;
    left: 0;

    .leftContainer {
        display: flex;
        align-items: center;
        gap: 10px;

        h1 {
            margin: 0;
            font-size: 1.5rem;
            font-weight: 700;
            letter-spacing: 0.5px;
        }
    }

    .rightContainer {
        .close-icon {
            width: 24px;
            height: 24px;
            fill: white;
        }
    }
}