// Index.scss

.index-container {
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center; // Center elements horizontally
    min-height: 100vh; // Full viewport height
    background-color: var(--background-color, #f9f9f9); // Optional background color
    color: var(--text-color, #333); // Optional text color
    text-align: center; // Center align text

    .index-heading {
        font-size: 2rem; // Heading size
        margin-bottom: 2rem; // Spacing below heading
        color: var(--primary-color, #26425A); // Primary color for heading
    }

    .button-container {
        display: flex;
        gap: 0.5rem; // Space between buttons
    }

    .index-button {
        padding: 0.75rem 1.5rem; // Padding inside buttons
        font-size: 1rem; // Button text size
        color: white; // Text color
        background-color: var(--button-color, #66A8CF); // Button background color
        border: none; // Remove default border
        border-radius: 5px; // Rounded corners
        cursor: pointer; // Change cursor on hover
        transition: background-color 0.3s; // Smooth background color transition

        &:hover {
            background-color: var(--button-hover-color, #26425A); // Change color on hover
        }

        &:focus {
            outline: none; // Remove default focus outline
            box-shadow: 0 0 0 3px rgba(102, 168, 207, 0.5); // Add custom focus shadow
        }
    }
}